@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@200&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Open+Sans&display=swap');

html {
  background: url(./images/bg.png) no-repeat center center fixed;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}
#header {
  color: white;
  font-family: 'Bebas Neue';
  font-size: 300px;
  line-height: 130px;
  padding-top: 225px;
  padding-left: 120px;
}
#bottom-header {
  color: white;
  font-family: 'Source Sans Pro';
  font-size: 129px;
  padding-left: 110px;
}
#navdiv {
  width: 600px;
  position: fixed;
  right: 98px;
  font-family: 'Open Sans';
}
#navbar {
  list-style: none;
  display: flex;
  justify-content: space-between;
  text-align: right;
}
#clickable {
  background-color: rgba(255, 255, 255, 0.14);
  color: white;
  font-size: 20px;
  width: 120px;
  height: 50px;
  border-radius: 50px;
  text-align: center;
  transition: 0.5s;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: -32px;
  margin-left: 50px;
}
a {
  text-decoration: none;
  color: white;
}
#clickable:hover {
  background-color: rgba(255, 255, 255, 0.26);
}
#social {
  padding-left: 114px;
  position: fixed;
  bottom: 50px;
}
#social a {
  padding-left: 10px;
}
#clickable1 {
  position: fixed;
  bottom: 50px;
  right: 70px;
  display: flex;
}
#clickable2 {
  background-color: rgba(255, 255, 255, 0.14);
  color: white;
  border-radius: 50px;
  text-align: center;
  transition: 0.5s;
  width: 120px;
  height: 50px;
  margin-left: -40px;
}
#clickable2:hover {
  background-color: rgba(255, 255, 255, 0.26);
  cursor: pointer;
}
